$background: rgba(0,0,0,0);

body{
    background: silver;
    >.asDragShadow{
        border:1px solid black;
        background-color: white;
        height:1.5em;
        width:5em;
        padding: 1px 4px;
        box-sizing: border-box;
        position: fixed;
        z-index: 1000;
        box-shadow: 5px 5px 5px #888888;
    }
}

.SBlock{
    &.Layout{
        position: absolute; 
        height: 600px;
        width:800px;
        left:20px;
        top:20px;
        background-color: $background;
    }

    .Anchor{ 
        overflow: hidden;
        //outline: 1px solid black;
        box-sizing: border-box;
        float: left; 
        position: relative;
        >.Drag{
            width:8px;
            height:8px;
            border:none;
            //background-color: $background; 
        }
        &.horizontal{
            position: relative;
            >.Drag{
                position: absolute;
                height:100%;
                margin-left: -6px; 
                cursor: w-resize;
                &.moving{
                    border-right: 4px solid gray;
                    margin-left: -10px; 
                }
            }
        }
        &.vertical{
            position: relative;
            >.Drag{
                position: absolute;
                width:100%;
                margin-top: -6px;
                cursor: n-resize;
                &.moving{
                    border-bottom: 4px solid gray;
                    margin-top: -10px; 
                }
            }
        }
        >.Tabs{ 
            margin:2px;
            height: calc(100% - 4px);
            box-sizing: border-box;
            display: flex;
            flex-direction: column;
            //border:1px solid silver;
            //border-radius: 5px;
            overflow: hidden;
            background-color: white;
            >.head{ 
                flex: 0 0 auto;
                background-color: rgba(0, 0, 0, 0.05);
                span{ 
                    display: inline-block;
                    white-space: nowrap;
                    user-select: none;
                    cursor: pointer;
                    &.selected{
                        background-color: white;
                    }
                    padding: 2px 8px;
                    >.Drag{
                        display: inline;
                        border: none;
                    }
                }
            }
            >.body{
                flex: 1 1; 
                padding: 2em;
                text-align: center;
            } 
        }
        >.dropZone{
            position: absolute;
            left:0;top:0;bottom:0;right:0;
            //background: rgba(0, 0, 0, 0.05);
            &:hover{
                background:  rgba(0, 0, 0, 0.3);
                outline: 2px solid black ;
            } 
            &.centre{
                //display: none;
            }
        }
    } 

    .dropZone{
        display: none;
        &.dropped{
            display: block;
            background-color: red;
        }
    }
    &.Layout.dragging .dropZone{
        display: block; 
    }
}

.Drag{
    border:1px solid black;
    width:4px;
    height:4px;
    user-select: none;  
    z-index: 1000; 
}