body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background: silver; }
  body > .asDragShadow {
    border: 1px solid black;
    background-color: white;
    height: 1.5em;
    width: 5em;
    padding: 1px 4px;
    box-sizing: border-box;
    position: fixed;
    z-index: 1000;
    box-shadow: 5px 5px 5px #888888; }

.SBlock.Layout {
  position: absolute;
  height: 600px;
  width: 800px;
  left: 20px;
  top: 20px;
  background-color: rgba(0, 0, 0, 0); }

.SBlock .Anchor {
  overflow: hidden;
  box-sizing: border-box;
  float: left;
  position: relative; }
  .SBlock .Anchor > .Drag {
    width: 8px;
    height: 8px;
    border: none; }
  .SBlock .Anchor.horizontal {
    position: relative; }
    .SBlock .Anchor.horizontal > .Drag {
      position: absolute;
      height: 100%;
      margin-left: -6px;
      cursor: w-resize; }
      .SBlock .Anchor.horizontal > .Drag.moving {
        border-right: 4px solid gray;
        margin-left: -10px; }
  .SBlock .Anchor.vertical {
    position: relative; }
    .SBlock .Anchor.vertical > .Drag {
      position: absolute;
      width: 100%;
      margin-top: -6px;
      cursor: n-resize; }
      .SBlock .Anchor.vertical > .Drag.moving {
        border-bottom: 4px solid gray;
        margin-top: -10px; }
  .SBlock .Anchor > .Tabs {
    margin: 2px;
    height: calc(100% - 4px);
    box-sizing: border-box;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    overflow: hidden;
    background-color: white; }
    .SBlock .Anchor > .Tabs > .head {
      -webkit-flex: 0 0 auto;
              flex: 0 0 auto;
      background-color: rgba(0, 0, 0, 0.05); }
      .SBlock .Anchor > .Tabs > .head span {
        display: inline-block;
        white-space: nowrap;
        -webkit-user-select: none;
           -moz-user-select: none;
            -ms-user-select: none;
                user-select: none;
        cursor: pointer;
        padding: 2px 8px; }
        .SBlock .Anchor > .Tabs > .head span.selected {
          background-color: white; }
        .SBlock .Anchor > .Tabs > .head span > .Drag {
          display: inline;
          border: none; }
    .SBlock .Anchor > .Tabs > .body {
      -webkit-flex: 1 1;
              flex: 1 1;
      padding: 2em;
      text-align: center; }
  .SBlock .Anchor > .dropZone {
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0; }
    .SBlock .Anchor > .dropZone:hover {
      background: rgba(0, 0, 0, 0.3);
      outline: 2px solid black; }

.SBlock .dropZone {
  display: none; }
  .SBlock .dropZone.dropped {
    display: block;
    background-color: red; }

.SBlock.Layout.dragging .dropZone {
  display: block; }

.Drag {
  border: 1px solid black;
  width: 4px;
  height: 4px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  z-index: 1000; }

